<template>
  <div>
    <my-header></my-header>
  <div class="manage-switch">
    <permission-switch-box @tab-click="swichTab" :active-tab="active_tab"></permission-switch-box>
    <div class="switch-content">
      <component :is="active_tab"></component>
      <!--<permission-record></permission-record>
      <permission-settings></permission-settings>-->
    </div>
  </div>
  </div>
</template>

<script>
    import PermissionRecord from "../components/PermissionRecord";
    import PermissionSettings from "../components/PermissionSettings";
    import PermissionSwitchBox from "../components/PermissionSwitchBox";
    import MyHeader from "../components/MyHeader";
    export default {
      name: "PermissionManage",
      components: {MyHeader, PermissionSwitchBox, PermissionSettings, PermissionRecord},
      data(){
        return {
          active_tab: 'permission-record'
        }
      },
      methods:{
        swichTab(data){
          this.active_tab = data
        }
      }
    }
</script>

<style scoped>

</style>



