<template>
  <div class="historyTable-box">
    <table cellspacing="1" class="historyTable table">
      <thead>
      <tr>
        <th>权限操作内容</th>
        <th>权限操作时间</th>
        <th>权限操作人</th>
        <th>操作下载名单</th>
      </tr>
      </thead>
      <tbody>
      <!--<tr>
        <td>授权管理权限</td><td>2018-11-15   19：30</td><td>操作者账号</td><td><a href="javascript:;" class="btn-downRoll">下载名单</a></td>
      </tr>-->
      <tr v-for="(historys,index) in auth_history" :key="index">
        <td>{{historys.op_desc}}</td>
        <td>{{historys.record_time}}</td>
        <td>{{historys.op_user}}</td><td>
        <a :href="get_modified_auth_users(historys.record_id)" class="btn-downRoll">下载名单</a>
      </td>
      </tr>
      </tbody>
    </table>
    <div class="getDataErr" v-if="errTxt !== null">{{errTxt}}</div>
    <!--<my-paginator></my-paginator>-->
    <!--<div class="paginator">
      <p class="record-num">共 <span>15</span> 条记录</p>
      <a href="javascript:;" class="page-prev spr"></a>
      <a href="javascript:;" class="page">1</a>
      <a href="javascript:;" class="page">2</a>
      <a href="javascript:;" class="page-next spr"></a>
    </div>-->
  </div>
</template>

<script>
    import MyPaginator from "./MyPaginator";
    import {query_auth_history} from "../apis/getData";
    import {mapState} from "vuex";
    export default {
      name: "PermissionRecord",
      components: {MyPaginator},
      data(){
        return{
          auth_history:null,
          errTxt: null,
        }
      },
      computed: {
        ...mapState(["game_id"]),
      },
      mounted(){
        this.query_history();
      },
      methods:{
        get_modified_auth_users(record_id){
          let url = '/cgi-bin/report_judge/qq/get_modified_auth_users?';
          let params ={
            game_id: this.game_id,//游戏id, 取0表示全量游戏
            src_type: 0,
            op_type: 0,//只查询自己的操作记录，1查询全量的查询记录
            record_id: record_id,//记录id，query_modify_auth_history接口中获取
          };
          for (var [k, v] of Object.entries(params)) {
            url+= k + "=" + v + "&";
          };
          return url;
        },
        query_history(){
          const params = {
            game_id: this.game_id,//游戏id, 取0表示全量游戏
            //user_id: 0,//用户id写0，实际从cookie中获取
            src_type: 0,
            op_type: 0,//只查询自己的操作记录，1查询全量的查询记录
            start_time:0,// 开始时间
            end_time: 0,//结束时间
            offset: 0,//查询偏移量
            limit: 0,//本次要查询的个数注：start_time, end_time, offset, limit一期应该用不着
          };
          query_auth_history(params).then( response => {
            if (response.ret === 0) {
              this.auth_history = response.data.history;
              if(response.data.history.length === 0){
                this.errTxt = "暂无数据";
              }
            }else if (response.ret === this.$code.LOGINTIMEOUT) {
              this.$loginOut();
            }else{
              this.errTxt = "数据请求失败，请尝试刷新页面，以重新获取数据";
            }
          });
        }
      }
    }
</script>

<style scoped>

</style>



