<template>
  <div class="set-box">
    <form enctype="multipart/form-data" name="fileinfo" id="myForm" ref="myForm">
    <div class="power-level widths">
      <span>权限级别</span>
      <!--权限级别的文案是： 管理者权限，审核权限 - C级，审核权限 - B级，，审核权限 - A级-->
      <!--操作状态的文案是： 授予权限，取消权限-->
      <select name="dst_level" id="selectLevel" v-model="formData.dst_level">
        <!--<option selected="selected" disabled="disabled"  style='display: none' value=''></option>-->
        <option value="null" disabled selected hidden>请选择权限级别</option>
        <option value="0" v-if="Number(admin_level) === 1">普通管理者</option>
        <option value="1">A级</option>
        <option value="2">B级</option>
        <option value="3">C级</option>
      </select>
    </div>
    <div class="power-state widths">
      <span>权限操作状态</span>
      <select name="op_type" id="selectState" v-model="formData.op_type">
        <option value="null" disabled selected hidden>请选择授权/取消</option>
        <option value="1" v-if="Number(admin_level) === 1">授予权限</option>
        <option value="2">取消权限</option>
      </select>
    </div>
    <div class="roll widths">
      <span>名单</span>
      <div class="roll-font">
        <!--<p><span></span></p>-->
        <textarea value=""
                  name="modify_users"
                  class="clearfix"
                  id="roll"
                  placeholder="名单类型：QQ号或微信号
请输入名单，每行仅限一个QQ账号
或者微信账号，请以空格键隔开"
                  v-model="formData.modify_users"
        ></textarea>
        <div class="roll-list">
            <input type="file" @change="getFile($event)"/>
        </div>

        <div class="roll-model">
          <div>名单文件模板下载：
            <a download="user_template.txt"
               href="/static/report_judge/admin/static/user_template.txt">
              管理平台授权模板
            </a>
          </div>
        </div>
      </div>
    </div>

    <div class="desc widths">
      <span>授权描述</span>
      <div class="roll-font">
        <textarea value=""
                  class="clearfix"
                  v-model="formData.comment"
                  name="comment"
        ></textarea>
        <button class="btn-submit"
                type="button"
                @click="checkForm()"
        >提交处理</button>
      </div>
    </div>
    </form>
    <my-dialog></my-dialog>
  </div>
</template>

<script>
    import {setting_auth_file} from "../apis/getData";
    import query_admin_auth from "../mixins/query_admin_user_auth"
    import MyDialog from "./MyDialog";
    import {getStore} from "../assets/js/storage";
    import {mapState} from "vuex";
    import Qs from "qs"
    export default {
      name: "PermissionSettings",
      components: {MyDialog},
      mixins: [query_admin_auth],
      data(){
        return{
          formData:{
            dst_level: null,
            op_type: null,
            comment: null,
            modify_users: [],
            auth_user_file: null,
          },
          formErr: null,
          level: null
        }
      },
      computed:mapState({
        game_id: state => state.game_id,
        admin_level(){
          return getStore("admin_level") || this.level;
        },
      }),
      mounted(){
        if(typeof this.admin_level === "object"){
          this.query_admin_auth();
        };
      },
      methods:{
        checkForm(){
          if(this.formData.dst_level === null){
            this.formErr = '权限级别';
            this.$onlyTxtPopShow(`'提交失败 – 请填写“${this.formErr}”内容，填写后再提交'`);
          }else if(this.formData.op_type === null){
            this.formErr = '授权/取消';
            this.$onlyTxtPopShow(`'提交失败 – 请填写“${this.formErr}”内容，填写后再提交'`);
          }else if(this.formData.comment === null){
            this.formErr = '授权描述';
            this.$onlyTxtPopShow(`'提交失败 – 请填写“${this.formErr}”内容，填写后再提交'`);
          }else if(this.formData.modify_users.length === 0 && this.formData.auth_user_file === null){
            this.formErr = '输入名单/上传名单';
            this.$onlyTxtPopShow(`'提交失败 – 请填写“${this.formErr}”内容，填写后再提交'`);
          }else{
            setting_auth_file(
              this.candleParams().params,
              this.candleParams().config,
            ).then(response => {
              let setDataTxt = "";
              let dst_level_txt = Number(this.formData.dst_level) === 0 ? "管理权限" : "审判权限";
              let op_type_txt = Number(this.formData.op_type) === 1 ? "授权" : "取消";
              if (response.ret === 0) {
                if(Number(this.formData.op_type) === 1){
                  setDataTxt = `${op_type_txt}成功 – “${dst_level_txt}”${op_type_txt}成功，请知会被授权人确认；`;
                }else{
                  setDataTxt = `取消成功 – ”${dst_level_txt}“取消成功；`;
                };
                this.formReset();
              }else if (response.ret === this.$code.NOAUTHORITY.num) {
                setDataTxt = this.$code.NOAUTHORITY.txt;
              }else if (response.ret === this.$code.INCORRECTFORMAT.num) {
                setDataTxt = this.$code.INCORRECTFORMAT.txt;
              }else if (response.ret === this.$code.REPETEOPERATION.num ) {
                setDataTxt = this.$code.REPETEOPERATION.txt;
              }else if (response.ret === this.$code.LOGINTIMEOUT) {
                this.$loginOut();
              }else{
                setDataTxt = `${op_type_txt}失败 – ”${dst_level_txt}“${op_type_txt}失败，请重新尝试；`;
              };
              this.$onlyTxtPopShow(setDataTxt);
            })
          }
        },
        candleParams(){
          let tableParams = {
            game_id: this.game_id,//游戏id, 取0表示全量游戏
            src_type: 0,
            op_type: this.formData.op_type,//1：授权，2：取消(可恢复）；3：彻底删除；4：恢复
            modify_users: this.formData.modify_users,//用户列表, “名单”输入框中的数据。授予权限的时候，需要以:分割的qq与open_id组合,如果是取消权限只需给出uin列表
            dst_level: this.formData.dst_level,//设置的目标等级，0：普通管理者权限，1:A类审核权限, 2:B类审核权限, 3: C类审核权限
            comment: this.formData.comment,//权限变更时的评论语,没有则为空
          };
          //处理modify_users参数 start
          if(this.formData.modify_users.length > 0){
            let regex = /[\s\n]/;
            tableParams.modify_users = this.formData.modify_users.split(regex);
            tableParams.modify_users = JSON.stringify(tableParams.modify_users);
          }else{
            tableParams.modify_users = JSON.stringify([]);
          }
          //处理modify_users参数 end
          let config = null;
          let params = null;
          //处理文件上传的参数
          if(this.formData.auth_user_file !== null){//auth_user_file如果上传文件了，就不手动输入名单了
            let formData = new FormData();
            formData.append('auth_user_file', this.formData.auth_user_file);
            formData.append('op_type', this.formData.op_type);
            formData.append('modify_users', this.formData.modify_users);
            formData.append('dst_level', this.formData.dst_level);
            formData.append('comment', this.formData.comment);
            formData.append('game_id', this.game_id);
            formData.append('src_type', 0);
            params = formData;
            config = {
              headers: {'Content-Type':'multipart/form-data'}
            }
          }else{
            params = tableParams;
            //处理modify_users参数
            params = Qs.stringify(params);//处理参数
            config = {
              headers: {'Content-Type': 'application/x-www-form-urlencoded'}
            }
          };
          return {
            config: config,
            params: params
          };
        },
        getFile(event) {
          this.formData.auth_user_file = event.target.files[0];
        },
        formReset(){
          this.formData = {
            dst_level: null,
            op_type: null,
            comment: null,
            modify_users: [],
            auth_user_file: null,
          };
          this.$refs.myForm.reset();//重置表单
        },
      }

    }
</script>

<style scoped>

</style>



