<template>
  <div class="switch-box">
    <a href="javascript:;"
       class="btn-history "
       @click="$emit('tab-click','permission-record')"
       :class="{'on': activeTab == 'permission-record'}"
    >权限操作记录</a>
    <a href="javascript:;"
       class="btn-set"
       @click="$emit('tab-click','permission-settings')"
       :class="{'on': activeTab == 'permission-settings'}"
    >权限设置</a>
  </div>
</template>

<script>
    export default {
      name: "PermissionSwitchBox",
      props: ["activeTab"],
    }
</script>

<style scoped>

</style>



